import React from 'react'
import moment from 'moment'
import {
  Box,
  Button,
  Collapsible,
  Grid,
} from 'grommet'

export default ({
  stop,
  getDepartures,
  index,
}) => (
  <Button
    onClick={() =>
      getDepartures(stop.id, index)
    }
  >
    <Box elevation="large">
      <Box border>
        {stop.distance} meter:{' '}
        {stop.name}
        {Object.entries(
          stop.products
        ).map(([key, value]) => {
          if (value === true)
            return ' ' + key + ' '
        })}
      </Box>
      <Collapsible
        open={stop.departures}
      >
        {stop.departures &&
          stop.departures.map(
            (departure, index) => (
              <Grid
                key={index}
                columns={{
                  count: 3,
                  size: 'auto',
                }}
              >
                <Box>
                  {departure.direction}
                </Box>
                <Box>
                  {departure.line.id}
                </Box>
                <Box>
                  {moment(
                    departure.when
                  ).fromNow()}
                </Box>
              </Grid>
            )
          )}
      </Collapsible>
    </Box>
  </Button>
)
