import React from 'react'
import {
  Box,
  Button,
  Grommet,
  Text,
} from 'grommet'
import moment from 'moment'
import Layout from '../../components/Layout'
import Stop from './Stop'

class Bvg extends React.Component {
  state = {
    latitude: '',
    longitude: '',
    stops: [],
  }

  getLocation = () => {
    if (navigator.geolocation) {
      navigator.geolocation.getCurrentPosition(
        ({ coords }) => {
          this.setState({
            latitude: coords.latitude,
            longitude: coords.longitude,
          })
          this.getStops()
        }
      )
    }
  }

  getStops = () => {
    const {
      latitude,
      longitude,
    } = this.state
    const url =
      'https://1.bvg.transport.rest/stations/nearby?' +
      'latitude=' +
      latitude +
      '&longitude=' +
      longitude
    fetch(url)
      .then(response => {
        return response.json()
      })
      .then(result => {
        console.log(
          'setting stops',
          result
        )
        this.setState({ stops: result })
      })
      .catch(err => console.log(err))
  }

  getDepartures = (id, index) => {
    console.log(
      'getting dep for id ' + id
    )

    console.log(this.state.stops[index])

    if (
      this.state.stops[index]
        .departures !== undefined
    ) {
      this.setState(prevState => {
        const newStops = [
          ...prevState.stops,
        ]

        newStops[
          index
        ].departures = undefined

        return { stops: newStops }
      })

      return
    }
    const url = `https://1.bvg.transport.rest/stations/${id}/departures`

    fetch(url)
      .then(response => response.json())
      .then(result => {
        console.log(result)
        this.setState(prevState => {
          const newStops = [
            ...prevState.stops,
          ]

          newStops[
            index
          ].departures = result

          return {
            stops: newStops,
          }
        })
      })
      .catch(err => console.log(err))
  }

  render() {
    const { stops } = this.state
    return (
      <Layout>
        <Box
          justify="center"
          align="center"
        >
          {stops.length === 0 && (
            <Button
              onClick={this.getLocation}
              label="Get stops nearby"
            />
          )}
          <Box>
            {stops.length > 0 &&
              stops.map(
                (stop, index) => (
                  <Box
                    border
                    margin="small"
                    key={index}
                  >
                    <Stop
                      stop={stop}
                      index={index}
                      getDepartures={
                        this
                          .getDepartures
                      }
                    />
                  </Box>
                )
              )}
          </Box>
        </Box>
      </Layout>
    )
  }
}

export default Bvg
